import './karbantartas.css';
import { Container, Row, Col } from "react-bootstrap";
import { faEnvelopeOpenText, faHammer } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const Karbantartas = () => {
    return(
        <section className="karbantartas" id="karbantartas">
            <Container>
                <h1>Rendszeres Karbantartás</h1>
                <Row className='align-items-center'>
                    <Col xs={12} md={4} lg={4}>
                        <h2 className='kar-h2'>Negyedéves, havi vagy egyedi ütemezés</h2>
                        <p>Ügyfeleinkkel közösen alakítjuk ki a karbantartás gyakoriságát, hogy az mindig megfeleljen az égető használati szokásainak és terhelésének.</p>
                    </Col>
                    
                    <Col xs={6} md={2} lg={2}>
                        <FontAwesomeIcon className="ikon elso" icon={faHammer} size="5x"/>
                    </Col>
                    <Col xs={6} md={2} lg={2}>
                         <FontAwesomeIcon className="ikon masodik" icon={faEnvelopeOpenText} size="5x"/>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <h2 className='kar-h2'>Apró hibák elhárítása</h2>
                        <p>A karbantartások során észlelt kisebb repedések, szigetelési gondok vagy elmozdult alkatrészek javításával megelőzzük a későbbi komolyabb meghibásodásokat.</p>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}