import {Container} from 'react-bootstrap';
import './bemutatkozas.css';

export const Bemutatkozas = () => {
    return(
        <section className='bemutatkozas' id='bemutatkozas'>
            <Container>
                <br></br>
                <br></br>
                <h1>Bemutatkozás</h1>
                <p className="udvozles">Üdvözöljük a TűzTérTech világában, ahol a szakértelem és az innováció találkozik, hogy a lehető legtöbbet hozhassa ki égetőberendezéseiből!</p>
                <br></br>
                <p className='bemut'>Több mint két évtizede dolgozunk azon, hogy ügyfeleink telephelyein a tűzterek ne csak működjenek, hanem hosszú távon is maximális hatékonyságot és üzembiztonságot nyújtsanak. Ez idő alatt számos ipari létesítménynek segítettünk átalakítani, modernizálni vagy épp felújítani az égetőrendszereit - legyen szó <span className='kiemel'>Addfield, Vulcan</span> vagy más típusokról.</p>
                <br></br>
                <p className='bemut'>Ami igazán megkülönböztet minket, az a <span className='kiemel'>gyakorlati tapasztalat</span> és az állandó <span className='kiemel'>fejlesztési szemlélet</span>. Olyan egyedi megoldásokat dolgoztunk ki, amelyekkel csökkenthető a meghibásodások kockázata, miközben jelentősen növelhető a berendezések élettartama. Nem csupán gyorsan reagálunk, de <span className='kiemel'>helyben</span> javítunk, így Önnek nem kell drága és időigényes szállítással bajlódnia.</p>
                <br></br>
                <p className='bemut'><span className='kiemel'>Célunk egyszerű</span>: hosszú távú partnerei szeretnénk lenni minden olyan vállalkozásnak, amely kiemelt figyelmet fordít a működés biztonságára és a környezet védelmére. Bízza ránk égetőit, és tapasztalja meg, milyen, amikor egy professzionális csapat vállal felelősséget az Ön berendezéseinek zavartalan működéséért!</p>
            </Container>
        </section>
    );
}