import './Referenciak.css';
import {Container} from 'react-bootstrap';

export const Referenciak = () => {
    return(
        <section className='referenciak' id='referenciak'>
            <Container className='referenciak-container'>
                <h2>Hogyan mentettünk meg egy <br/> <span className='fejlec'>kipúposodott tűzteret hétvégén?</span></h2>
                <Container className='card'>
                    <p>Nemrég egy nagy állattartó telephez riasztottak minket, ahol ismeretlen okból a tűztér fala több helyen kipúposodott, és furcsa égésnyomok jelentek meg. Először azt gondolhatták, hogy a szigetelés nem bírta a hőt, de a helyszínen készült fotókat elemezve kiderült: az égőfej elmozdult, ami félrefújta a lángot, így a szigetelés kezdett tönkremenni.
                    A telepre szigorú beléptetési szabályok miatt csak előre egyeztetett időpontban lehetett bejutni, ezért azonnal telefonáltunk az üzemvezetőnek, aki még hétvégén is kész volt minket fogadni. Néhány órás munkával sikerült stabilizálni a tűzteret, és a nagyobb cserék elvégzéséig is biztonságosan használhatóvá tettük a berendezést. Ezáltal a telep elkerülte a termeléskiesést és jelentős veszteségeket.
                    </p>
                    <p className='idezet'>„A Tűztér Tech csapata gyorsan reagált és a helyszínen oldotta meg a problémát. Máskülönben le kellett volna állítanunk az egész üzemet.”</p>
                    <p className='nev'>— Állattartó telep vezetője</p>
                </Container>
            </Container>
        </section>
    )
}