import { Container, Row, Col } from "react-bootstrap";
import './Szolgaltatasaink.css';
import {faBrain} from '@fortawesome/free-solid-svg-icons';
import { faScaleBalanced } from "@fortawesome/free-solid-svg-icons";
import { faHandFist } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


export const Szolgaltatasaink = () => {
  return (
    <section className="szolgaltatasaink" id="szolgaltatasaink">
        <Container>
        <h1>Szolgáltatásaink</h1>
        <h2>Javítás és felújítás</h2>
          <Row className='align-items-center'>
            <Col xs={12} md={6} xl={6}>
              <ul>
                <li className='list-item'>
                  <FontAwesomeIcon className='ikon' icon={faBrain} size="3x"/>
                  <div className='text-content'>
                    <h2>Helyszíni diagnosztika</h2>
                    <p>Első lépésben alaposan felmérjük az égető aktuális állapotát: vizsgáljuk a belső falazatot, a szigetelést, az égőfejet, valamint az elektronikai és mechanikai elemeket.</p>
                  </div>
                </li>
                <li className='list-item'>
                <FontAwesomeIcon className='ikon balance' icon={faScaleBalanced} size="2x"/>
                  <div className='text-content'>
                    <h2>Célzott beavatkozás</h2>
                    <p>A meghibásodott vagy elavult alkatrészeket a helyszínen cseréljük, javítjuk. Nem kell a készüléket leszerelni és máshová szállítani, ezzel időt és költséget takarítunk meg.</p>
                  </div>
                </li>
                <li className='list-item'>
                  <FontAwesomeIcon className='ikon' icon={faHandFist} size="3x"/>
                  <div className='text-content'> 
                    <h2>Modernizálás, hatékonyságnövelés</h2>
                    <p>Saját tapasztalataink és fejlesztéseink révén olyan apró módosításokat végzünk, amelyek növelik az égés hatásfokát, így csökkentve az üzemeltetési költségeket.</p>
                  </div>
                </li>
              </ul>
            </Col>
            <Col xs={12} md={6} xl={6} className='kep-col'>
            </Col>
          </Row>
        </Container>
    </section>
  )
}
