import { faBolt } from "@fortawesome/free-solid-svg-icons"
import { faIndustry } from "@fortawesome/free-solid-svg-icons";
import { faGraduationCap } from "@fortawesome/free-solid-svg-icons";
import { faCoins } from "@fortawesome/free-solid-svg-icons";
import { faLightbulb } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import {Row, Col, Container} from 'react-bootstrap';
import './Miertmi.css';

export const Miertmi = () => {
    return(
        <section class='miertmi' id='miertmi'>
            <Container className='miertmi-container'>
                <Row>
                    <h1>Miért a TűzTérTech?</h1>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faBolt} size="3x"/>
                            <h3>Villámgyors reagálás</h3>
                            <p className="miert-p">Tudjuk, mekkora veszélyt és költséget jelenthet egy álló égető, ezért mindent megteszünk, hogy minél hamarabb működőképes legyen a berendezés.</p>
                        </Container>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faIndustry} size="3x"/>
                            <h3>Helyszíni megoldások</h3>
                            <p className="miert-p">Nálunk nincs szükség a készülék szétszerelésére és elszállítására. A gondot általában helyben, egyetlen kiszállással orvosoljuk.</p>
                        </Container>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faGraduationCap} size="3x"/>
                            <h3>Magas szintű szakértelem</h3>
                            <p className="miert-p">Több mint húsz éve foglalkozunk tűzterekkel, különösen az Addfield és Vulcan égetőkkel. Tapasztalatunkra és egyedi fejlesztésű megoldásainkra mindig számíthat.</p>
                        </Container>
                    </Col>
                </Row>
                <Row>
                <Col xs={0} md={2} lg={2}>
                </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faCoins} size="3x"/>
                            <h3>Költséghatékonyság</h3>
                            <p className="miert-p">Azáltal, hogy az egész folyamatot az Ön telephelyén végezzük, rengeteg időt és pénzt spórolunk meg.</p>
                        </Container>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className="card">
                            <FontAwesomeIcon className='ikon' icon={faLightbulb} size="3x"/>
                            <h3>Innováció és fejlesztés</h3>
                            <p className="miert-p">Folyamatosan tesztelünk új anyagokat, alkatrészeket és javítási megoldásokat, hogy a berendezések élettartamát és hatékonyságát még tovább növelhessük.</p>
                        </Container>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}