import './Elofizetes.css';
import {Row, Col, Container} from 'react-bootstrap';

export const Elofizetes = () => {
    return(
        <section className='elofizetes'>
            <Container>
                <br></br>
                <h1>Előfizetéses konstrukciók</h1>
                <Row>
                    <Col xs={6} md={4} lg={4}>
                        <Container className='kartya'>
                            <h2>24-72 órás kiszállás </h2>
                            <p>Havi fix 20 000 Ft-os díj ellenében előfizetőinknél garantáljuk, hogy hiba esetén legfeljebb 72 órán belül, de sokszor már 24 órán belül is megjelenünk.</p>
                        </Container>
                    </Col>
                    <Col xs={6} md={4} lg={4}>
                        <Container className='kartya'>
                            <h2>Éves 300 000 Ft-os javítási csomag</h2>
                            <p>Az előfizetés részeként évente 300 ezer forintig díjmentesen végezzük el az esetlegesen szükséges javításokat.</p>
                        </Container>
                    </Col>
                    <Col xs={12} md={4} lg={4}>
                        <Container className='kartya'>
                            <h2>Előnyt élvez a többi ügyféllel szemben</h2>
                            <p>Az előfizető partnereink kiszolgálása mindig elsőbbséget élvez, így akár hétvégén vagy ünnepnapokon is számíthatnak ránk.</p>
                        </Container>
                    </Col>
                </Row>
            </Container>
        </section>
    )
}