import { Container, Row, Col } from "react-bootstrap";
import kep from '../assets/img/szereles.jpg';
import egeto1 from '../assets/img/egeto1.jpeg';
import egeto2 from '../assets/img/egeto2.jpeg';
import egeto3 from '../assets/img/egeto3.jpeg';
import egeto_utana1 from '../assets/img/egeto_utana1.jpg';
import egeto_utana2 from '../assets/img/egeto_utana2.jpg';
import egeto_utana3 from '../assets/img/egeto_utana3.jpg';
import './problemafelv.css';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleCheck } from '@fortawesome/free-solid-svg-icons';
import tab_pane from './tab_pane';
import ImageSlider from "./tab_pane";


export const Problemafelv = () => {

    const tabs1 = [
        {
          title: '1. kép',
          imageUrl: egeto1
        },
        {
          title: '2. kép',
          imageUrl: egeto2
        },
        {
          title: '3. kép',
          imageUrl: egeto3
        }
      ];

      const tabs2 = [
        {
          title: '1. kép',
          imageUrl: egeto_utana2
        },
        {
          title: '2. kép',
          imageUrl: egeto_utana3
        }
      ];

    return(
        <section className='problema' id='problema'>
            <Container>
                <Row className='align-items-center'>
                    <Col  xs={12} md={6} xl={6}>
                    <div className="sliderdiv" style={{ maxWidth: '600px', margin: '0 auto', height: '500px' }}>
                        <h2>Előtte: </h2>
                        <ImageSlider slides={tabs1}/>
                        <h2>Utána: </h2>
                        <ImageSlider slides={tabs2}/>
                    </div>
                    </Col>
                    <Col xs={12} md={6} xl={6}>
                        <h1>
                        Miért fontos a megbízható incinerátor?
                        </h1>
                        <p>
                        Egy égetőberendezés meghibásodása nem csupán anyagi vagy technikai gond: veszélyeztetheti az egész telep biztonságát, a termelést, és akár környezetvédelmi kihágásokat is okozhat. A telephelyen felgyűlő veszélyes hulladék, állati tetemek vagy egyéb megsemmisítendő anyagok komoly kockázatot jelentenek, ha az égető nem üzemel megfelelően.
                        </p>
                        <h2>
                        Milyen kockázatok vannak hiba esetén?
                        </h2>
                        <ul>
                            <li>
                                <div className='top-content'>                            
                                    <FontAwesomeIcon size="2x" className="ikon" icon={faCircleCheck} />
                                    <h3>Fertőzésveszély és egészségügyi kockázatok </h3>
                                </div>
                                    <p>Ha az állati tetemek, hulladékok elszállításra várnak, növekszik a fertőzések terjedésének esélye.</p>  
                            </li>
                            <li>
                                <div className='top-content'>                            
                                    <FontAwesomeIcon size="2x" className="ikon" icon={faCircleCheck} />
                                    <h3>Üzemszünet és termeléskiesés  </h3>
                                </div>
                                    <p>Egy váratlan leállás megakaszthatja a teljes telep működését, ami súlyos anyagi károkat okozhat.</p>  
                            </li>
                            <li>
                                <div className='top-content'>                            
                                    <FontAwesomeIcon size="2x" className="ikon" icon={faCircleCheck} />
                                    <h3>Folyamatos kiadások</h3>
                                </div>
                                    <p>Az égető meghibásodásakor sokszor drága szállítási, elszállíttatási és javítási költségekkel is számolni kell.</p>  
                            </li>
                        </ul>
                    </Col>
                    
                </Row>
                <h2>A gyors és hatékony megoldás: a TűzTérTech szervízcsapata</h2>
                <p>Mi <span className='highlight'>egyedi, helyszíni javítási technológiáinkkal</span>, több évtizedes tapasztalatunkkal és gyors reagálásunkkal biztosítjuk, hogy berendezései minél kevesebb időt álljanak. Nálunk nem kell hosszadalmas beszállításra vagy külön szervizállomásokra várnia: mindent igyekszünk <span className="highlight">ott és akkor megoldani</span>, amikor Önnek a legnagyobb szüksége van rá.</p>
            </Container>
        </section>
    )
}